  props: {
    isActive: vueTypes.bool.def(false),
    isInline: vueTypes.bool.def(false),
    label: vueTypes.string.def(''),
    title: vueTypes.string,
    clickHandler: vueTypes.func
  }
}
</script>

<template functional>
  <span
    class="premium-mark"
    :class="{
      'premium-mark_active': props.isActive,
      'premium-mark_inline': props.isInline,
      'premium-mark_clickable': props.clickHandler,
    }"
    :title="props.title"
    @click="() => { if (props.clickHandler) props.clickHandler() }"
  >{{ props.label }}</span>
</template>

