<script>
import vueTypes from 'vue-types'
import { mapGetters, mapActions } from 'vuex'
import config from '@/config/bookmarklet'
import HeaderBar from '@/components/HeaderBar'
import MarkdownTextarea from '@/components/MarkdownTextarea'
import SearchableSelect from '@/components/SearchableSelect'
import Toggler from '@/components/Toggler'
import ThreeDotsSpinner from '@/components/ThreeDotsSpinner'
import SvgStrokeIcon from '@/components/SvgStrokeIcon'
import Loader from '@/components/Loader'
import PremiumMark from '@/components/PremiumMark'
import { setInterfaceLanguage } from '@/i18n'

const LAST_USED_WIDGET_ID_COOKIE_NAME = config.cookieNameLastUsedWidget

const DROPDOWN_HEIGHT_OFFSET = 83

const NEW_GROUP_ID = 'new'
const ID_REGEXP = /^(\d+)_(.+)/

const SM_MESSAGE_GET_USER_PAGES = 'getUserPages'
const SM_MESSAGE_SIGN_IN = 'signIn'
const SM_MESSAGE_SIGN_OUT = 'signOut'
const WINDOW_CURRENT_BROWSER = 'chrome'
const SM_MESSAGE_OPEN_BOOKMARKS = 'openBookmarksManager'
const SM_MESSAGE_SHOW_HELP_PAGE = 'newtabHelp'
const SM_MESSAGE_IMPORT = 'importBookmarks'
const SM_MESSAGE_CLEAR_CACHE = 'clearCache'

const SM_OPTION_HOMEPAGE_URL = 'homepageUrl'
const SM_OPTION_HOMEPAGE_ID = 'homepageId'

const SETTINGS_TABS_ID = {
  SETTINGS: 'settings',
  SHORTCUTS: 'shortcuts',
}

export default {
  name: 'AddBookmarkPage',

  components: {
    HeaderBar,
    MarkdownTextarea,
    SearchableSelect,
    Toggler,
    ThreeDotsSpinner,
    SvgStrokeIcon,
    Loader,
    PremiumMark,
  },

  metaInfo () {
    return {
      title: this.$t('add_to_startme').message
    }
  },

  props: {
    propUrl: vueTypes.string,
    propTitle: vueTypes.string,
    isOptionsScreen: vueTypes.bool.def(false),
    hideOpenTabs: vueTypes.bool.def(false),
  },

  data () {
    return {
      title: this.propTitle || '',
      description: '',
      url: this.propUrl || '',
      isTitleEditing: false,
      selectedWidgetId: null,
      savedWidgetId: null,
      newWidgetTitle: '',
      newFolderTitle: '',
      isSaving: false,
      isLoading: true,
      isRefreshing: false,
      usedCache: false,
      filterString: '',
      showAdditionalFields: false,
      markdownGuideUrl: 'https://daringfireball.net/projects/markdown/basics',
      isSelectExpanded: false,
      hasWidgetFieldError: false,
      isImportingBookmarks: false,
      isEditingPreferences: this.isOptionsScreen || false,
      linkExists: null,
      denyLinksAutosaving: 1,
      linksAutosavingFetched: false,
      autosavedLink: null,
      autosavedLinkRemoved: false,
      isAutosaving: false,
      isAlreadyAutosaved: false,
      activeTabId: SETTINGS_TABS_ID.SETTINGS,
      browserActionShortcut: '',
      homepageShortcut: '',
      userPages: [],
      homepageOptions: [],
      homepageId: null,
      isClearingCache: false,
      isPageGetting: false,
      isTabsPermissionGranted: false,
      isOpenTabsOpened: false,
      openTabsArray: [],
      selectedOpenTabsArray: [],
      isUserMenuVisible: false,
      openTabsTitleEditing: null,
    }
  },

  computed: {
    ...mapGetters([
      'getUserData',
      'getWidgetsByPage',
      'getShouldCloseTabs',
    ]),

    allOpenTabsSelected () {
      return this.openTabsArray.every(tab => this.selectedOpenTabsArray.includes(tab.id))
    },

    isOpenTabsButtonActive () {
      return this.openTabsArray.length > 0
    },

    settingsTabs () {
      return [
        {
          id: SETTINGS_TABS_ID.SETTINGS,
          label: this.$t('settings').message,
        },
        {
          id: SETTINGS_TABS_ID.SHORTCUTS,
          label: this.$t('shortcuts').message,
        },
      ]
    },

    isSettingsTabActive () {
      return this.activeTabId === SETTINGS_TABS_ID.SETTINGS
    },

    isShortcutsTabActive () {
      return this.activeTabId === SETTINGS_TABS_ID.SHORTCUTS
    },

    isButtonActive () {
      if (this.selectedWidgetId !== null) return true
      if (!this.autosavedLink || (this.autosavedLink && (this.url !== this.autosavedLink.url || this.title !== this.autosavedLink.title || this.description !== this.autosavedLink.description))) return true

      return false
    },

    query () {
      const query = {}
      const queryString = window.location.search.substring(1)

      if (!queryString.length) return query

      const queryArray = queryString.split('&')

      queryArray.forEach((s) => {
        const parts = s.split('=')
        query[parts[0]] = decodeURIComponent(parts[1])
      })

      return query
    },

    dropdownHeight () {
      if (!this.$isMobile) return config.bookmarkletSearchableHeight

      const { height } = document.documentElement.getBoundingClientRect()

      return height ? height - DROPDOWN_HEIGHT_OFFSET : config.bookmarkletSearchableHeight
    },

    pages () {
      const widgets = this.getWidgetsByPage('urllist')

      return widgets.map((option) => {
        return {
          ...option,
          label: option.id === 'personal' ? '' : option.label,
          value: [
            ...option.value
              .filter(page => !page.isArchived)
              .map((page) => {
                let widgets = page.value

                if (widgets.length > 1) widgets.sort((a, b) => a.label.toLowerCase() > b.label.toLowerCase() ? 1 : -1)
                if (this.filterString) {
                  const re = new RegExp(this.filterString, 'i')
                  widgets = widgets.filter((w) => {
                    if (re.test(w.label)) return true
                    if (Array.isArray(w.value) && w.value.some(g => re.test(g.label))) {
                      w.value = w.value.filter(g => re.test(g.label))
                      return true
                    }

                    return false
                  })
                }

                if (!this.filterString) {
                  widgets.forEach((w) => {
                    if (Array.isArray(w.value)) {
                      w.value.push({
                        id: `${w.id}_${NEW_GROUP_ID}`,
                        label: `+${this.$t('create_new_group').message}`,
                        value: `${w.id}_${NEW_GROUP_ID}`,
                        omitHyphen: true,
                        isDimmed: true,
                      })
                    }
                  })

                  widgets.push({
                    id: -page.id,
                    label: `+${this.$t('create_new_widget').message}`,
                    value: -page.id,
                    isDimmed: true,
                  })
                }

                if (!widgets.length) return null

                return {
                  ...page,
                  value: [...widgets]
                }
              })
              .filter(page => page !== null)
          ],
        }
      })
    },

    selectedPage () {
      let selectedWidgetId = this.selectedWidgetId
      let page
      let widget

      if (typeof selectedWidgetId === 'string') {
        const parts = selectedWidgetId.match(ID_REGEXP)

        if (parts && parts[1]) {
          selectedWidgetId = Number(parts[1])

          this.pages.some((g) => {
            return g.value.some((p) => {
              const pageFound = p.value.some((w) => {
                if (w.id === selectedWidgetId) {
                  widget = w
                  return true
                }

                return false
              })

              if (pageFound) {
                page = p
                return true
              }

              return false
            })
          })
        }
      } else {
        this.pages.some((g) => {
          return g.value.some((p) => {
            let pageFound = false

            if (selectedWidgetId < 0 && p.id === Math.abs(selectedWidgetId)) {
              page = p
              return true
            } else if (selectedWidgetId > 0) {
              pageFound = p.value.some((w) => {
                if (w.id === selectedWidgetId) {
                  widget = w
                  return true
                }

                return false
              })

              if (pageFound) {
                page = p
                return true
              }

              return false
            }

            return false
          })
        })
      }

      if (page) {
        page.selectedWidget = widget
      }

      return page || {}
    },

    isNewGroupOptionSelected () {
      if (!this.selectedWidgetId || typeof this.selectedWidgetId !== 'string') return false

      const parts = this.selectedWidgetId.match(ID_REGEXP)

      if (!parts) return false

      return parts[2] && parts[2] === NEW_GROUP_ID
    },

    isNewWidgetOptionSelected () {
      return typeof this.selectedWidgetId === 'number' && this.selectedWidgetId < 0
    },

    profileAvatar () {
      return this.getUserData && this.getUserData.avatar_url ? this.getFacebookAvatarUrl(this.getUserData.avatar_url) || '' : ''
    },

    existedLinkWarningTitle () {
      if (!this.url || !this.linkExists || !this.linkExists.length) return ''

      const strArray = []

      this.linkExists.forEach((link) => {
        let str = ''

        if (link.team_public_id) str += `${link.team_title} / `
        str += `${link.page_title} / ${link.widget_title}`
        if (link.group_id) str += ` / ${link.group_name}`

        strArray.push(str)
      })

      return strArray.join('\r\n')
    },

    existedLinkWarningHref () {
      if (!this.url || !this.linkExists || !this.linkExists.length) return 'https://start.me/bookmarks'

      const url = this.url.replace(/\/$/, '')

      return this.$startme.primaryURL(`/bookmarks?section=search&q=${encodeURIComponent(url)}`)
    },

    selectedOpenTabs () {
      return this.openTabsArray.filter(tab => this.selectedOpenTabsArray.includes(tab.id))
    },
  },

  watch: {
    isNewWidgetOptionSelected (newValue) {
      this.$nextTick(() => {
        if (newValue && this.$refs.widgetTitleInput) this.$refs.widgetTitleInput.focus()
      })
    },
    isNewGroupOptionSelected (newValue) {
      this.$nextTick(() => {
        if (newValue && this.$refs.folderTitleInput) this.$refs.folderTitleInput.focus()
      })
    },
    propTitle (newTitle) {
      this.title = newTitle
    },

    propUrl (newUrl = '') {
      this.url = newUrl.replace(/\/$/, '')
    },

    denyLinksAutosaving () {
      if (!this.linksAutosavingFetched) return

      this.processChangeLinksAutosaving()
    },

    url: 'checkLink',

    isLoading (value) {
      if (!value && this.usedCache) this.fetchAllPages({ isRequestForced: true, noCache: true })
    },

    isSelectExpanded (value) {
      if (value) this.$eventBus.$emit('activate-searchable-input')
    },

    isEditingPreferences (state) {
      if (state) this.initPages()
    },

    homepageId (newId) {
      if (newId) this.setHomePage(newId)
    },
  },

  created () {
    this.$eventBus.$on('keypress-escape', this.handleEscape)
    this.setShortcuts()

    if (this.isOptionsScreen) {
      this.initPages()
      return
    }

    this.usedCache = !!(this.$store.state.allPages && this.$store.state.allPages.length)
  },

  async beforeMount () {
    this.checkLanguage()
    this.checkTabsPermission()
  },

  mounted () {
    this.fetchAllPages({ noCache: true, isCacheForced: true })
      .then(() => {
        return this.checkAutosaving()
      })
      .then(() => {
        this.initBookmarkForm()
        this.checkLink()
      })
  },

  beforeDestroy () {
    this.$eventBus.$off('keypress-escape', this.handleEscape)
  },

  methods: {
    ...mapActions([
      'addWidget',
      'autosaveLink',
      'autosaveLinks',
      'checkLinkExists',
      'addNewBookmark',
      'addBookmarksBatch',
      'editAutosavedLink',
      'removeAutosavedLink',
      'fetchAllPages',
      'checkLinksAutosaving',
      'changeLinksAutosaving',
      'toggleCloseTabs',
    ]),

    async editOpenTabsTitle (id) {
      if (this.$refs[`openTabsTitleInput_${id}`] && this.$refs[`openTabsTitleInput_${id}`][0]) {
        this.$refs[`openTabsTitleInput_${id}`][0].select()
      }
    },

    stopEditingOpenTabsTitle () {
      this.openTabsTitleEditing = null
    },

    checkLanguage () {
      chrome.storage.local.get(['cachedUser'])
        .then((result = {}) => {
          let cachedUser
          try {
            cachedUser = JSON.parse(result.cachedUser)
            if (cachedUser?.user?.lang) setInterfaceLanguage(cachedUser.user.lang)
          } catch (e) { }
        })
        .catch((e) => { console.warn(e) })
    },

    async editTitle () {
      this.isTitleEditing = true
      await this.$nextTick()
      if (this.$refs.titleInput) this.$refs.titleInput.select()
    },

    closeTitleEditing () {
      this.isTitleEditing = false
    },

    initPages (options = {}) {
      const userPages = this.$store.getters.getUserPages

      if (!options.forceFetch && userPages && userPages.length) {
        this.setupUserPages(userPages)
      } else {
        this.isPageGetting = true
        chrome.runtime.sendMessage({ message: SM_MESSAGE_GET_USER_PAGES, noCache: true })
          .then((pages) => {
            if (!pages) return

            this.$store.dispatch('setUserPages', pages)

            this.setupUserPages(pages)
            this.isPageGetting = false
          })
          .catch(() => {
            this.isPageGetting = false
          })
      }
    },

    setupUserPages (pages = []) {
      this.userPages = pages

      this.generatePages().then((id) => { this.homepageId = id })
    },

    setHomePage (homepageId) {
      chrome.storage.local.set({ [SM_OPTION_HOMEPAGE_ID]: homepageId })

      let homepage = this.userPages.find(page => page.id === homepageId)

      if (homepage && homepage.public_id) chrome.storage.local.set({ [SM_OPTION_HOMEPAGE_URL]: `/p/${homepage.public_id}` })
      else chrome.storage.local.remove([SM_OPTION_HOMEPAGE_URL])
    },

    generatePages () {
      return new Promise((resolve) => {

        this.homepageOptions = this.groupPagesByTeams()

        // See what page has been selected
        let homepageId = null

        chrome.storage.local.get([SM_OPTION_HOMEPAGE_ID], (result) => {
          try {
            homepageId = parseInt(result[SM_OPTION_HOMEPAGE_ID]) || null
          } catch (e) { resolve(homepageId) }

          // Make sure there is a homepage
          if (!homepageId && this.userPages.length > 0) {
            homepageId = this.userPages[0].id
          }

          resolve(homepageId)
        })
      })
    },

    groupPagesByTeams () {
      const userPages = this.userPages.filter(p => !p.is_archived)

      const personalPagesGroup = {
        title: 'Personal Pages',
        pages: userPages
          .filter(page => !page.organization_id)
          .map((page) => {
            return {
              id: page.id,
              label: page.title,
              value: page.id,
            }
          }),
      }

      const teamsGroups = []
      const teamPages = userPages.filter(page => !!page.organization_id)

      teamPages.forEach((page) => {
        const team = teamsGroups.find(item => item.id === page.organization_id)
        const pageOption = {
          id: page.id,
          label: page.title,
          value: page.id,
        }

        if (team !== undefined) {
          const index = teamsGroups.findIndex(item => item.title === team.title)
          teamsGroups[index].pages.push(pageOption)

          return
        }

        teamsGroups.push({ id: page.organization_id, title: page.organization_title, pages: [pageOption] })
      })

      const groups = [...teamsGroups]

      if (personalPagesGroup.pages.length > 0) groups.unshift(personalPagesGroup)

      return groups
    },

    emitOpenBookmarks () {
      chrome.runtime.sendMessage({ message: SM_MESSAGE_OPEN_BOOKMARKS })
    },

    setShortcuts () {
      chrome.commands.getAll()
        .then((shortcuts = []) => {
          const browserActionShortcutObject = shortcuts.find(sc => sc.name === '_execute_action')
          const homepageShortcutObject = shortcuts.find(sc => sc.name === 'open_startme_homepage')

          if (browserActionShortcutObject) this.browserActionShortcut = browserActionShortcutObject.shortcut
          if (homepageShortcutObject) this.homepageShortcut = homepageShortcutObject.shortcut
        })
        .catch((err) => { })
    },

    getFacebookAvatarUrl (url) {
      const isFBUrl = /graph\.facebook\.com\//.test(url)

      if (!isFBUrl) return url

      return `${url}?access_token=256485647863321|df5689481a09e57450817f458ebc7cf8`
    },

    checkLink () {
      return new Promise((resolve, reject) => {
        this.resetLinkExists()

        if (!this.url) {
          resolve({})
          return
        }

        this.checkLinkExists({ url: encodeURIComponent(this.url) })
          .then((data = {}) => {
            if (data.exists && data.exists.length) this.setLinkExists(data.exists)
            resolve(data)
          })
          .catch(reject)
      })
    },

    checkAutosaving () {
      return new Promise((resolve) => {
        this.checkLinksAutosaving()
          .then((data = {}) => {
            if (data.not_logged_in) {
              chrome.runtime.sendMessage({ message: SM_MESSAGE_SIGN_IN })
              this.quit()
              resolve()
              return
            }

            this.denyLinksAutosaving = Number(data.deny_links_autosaving)
            if (!data.deny_links_autosaving) this.processAutosaveLink()

            this.$nextTick(() => {
              this.linksAutosavingFetched = true
              resolve()
            })
          })
          .catch(() => { resolve() })
      })
    },

    processChangeLinksAutosaving () {
      const requestData = { denyLinksAutosaving: !!this.denyLinksAutosaving }

      this.changeLinksAutosaving(requestData)
        .then(() => {
          if (!this.denyLinksAutosaving && !this.autosavedLink) this.processAutosaveLink()
        })
        .catch(() => {})
    },

    processAutosaveLink () {
      this.isAutosaving = true
      const bookmark = {
        url: this.url,
        title: this.title,
        preventDuplication: true
      }

      this.autosaveLink(bookmark)
        .then((data) => {
          if (data && data.bookmark) {
            this.autosavedLink = data.bookmark
            this.autosavedLink.description = this.autosavedLink.description || ''
            this.isAlreadyAutosaved = data.exists || false
          } else {
            this.autosavedLinkRemoved = true
          }
          this.isAutosaving = false
        })
        .catch(() => {
          this.isAutosaving = false
        })
    },

    setWidgetFieldError () {
      this.hasWidgetFieldError = true
    },

    resetWidgetFieldError () {
      this.hasWidgetFieldError = false
    },

    activateSettingsTab (id) {
      this.activeTabId = id || SETTINGS_TABS_ID.SETTINGS
    },

    handleEscape () {
      if (this.isSelectExpanded) this.collapseSelect()
      else this.quit()
    },

    saveID (id) {
      this.savedWidgetId = id
    },

    restoreID () {
      this.newWidgetTitle = ''
      this.newFolderTitle = ''

      if (this.savedWidgetId === null) {
        this.setWidgetId(null)
        return
      }

      if (typeof this.savedWidgetId === 'number' && this.savedWidgetId > 0) {
        this.setWidgetId(this.savedWidgetId)
        this.savedWidgetId = null
      } else this.setWidgetId(this.getWidgetDefault())
    },

    initBookmarkForm () {
      // Set selected widget ID based on last used widget ID stored in browser storage
      chrome.storage.local.get([LAST_USED_WIDGET_ID_COOKIE_NAME])
        .then((result = {}) => {
          if (result[LAST_USED_WIDGET_ID_COOKIE_NAME] !== undefined) {
            this.selectedWidgetId = result[LAST_USED_WIDGET_ID_COOKIE_NAME]

            this.isLoading = false
            return
          }

          this.isLoading = false
        })
        .catch((e) => { })
    },

    getWidgetDefault () {
      let pageWithWidgets = null
      this.pages.some((pagesGroup) => {
        if (!pagesGroup.value || !pagesGroup.value.length) return false

        return pagesGroup.value.some((page) => {
          if (page.value && page.value.length > 1) {
            pageWithWidgets = page
            return true
          }

          return false
        })
      })

      if (!pageWithWidgets) return null
      if (Array.isArray(pageWithWidgets.value[0].value)) return pageWithWidgets.value[0].value[0].id

      return pageWithWidgets.value[0].id
    },

    sendChangesMultiple () {
      if (!this.selectedOpenTabsArray.length) return

      let bookmarks

      if (this.selectedWidgetId === null) {
        bookmarks = this.selectedOpenTabs.map((tab) => {
          return {
            url: tab.url,
            title: tab.title,
          }
        })

        this.isSaving = true

        if (bookmarks.length === 1) {
          this.autosaveLink(bookmarks[0])
            .then(() => {
              this.isSaving = false
              this.setCookieData(null)

              if (this.getShouldCloseTabs) chrome.tabs.remove(this.selectedOpenTabsArray)

              this.quit()
            })
            .catch(() => {})

          return
        }

        this.autosaveLinks(bookmarks)
          .then(() => {
            this.isSaving = false
            this.setCookieData(null)

            if (this.getShouldCloseTabs) chrome.tabs.remove(this.selectedOpenTabsArray)

            this.quit()
          })
          .catch(() => {})

        return
      }

      let isNewGroupOptionSelected = false
      let selectedWidgetId
      let folderId

      if (typeof this.selectedWidgetId === 'string') {
        const parts = this.selectedWidgetId.match(ID_REGEXP)

        if (!parts) return

        selectedWidgetId = parts[1]
        folderId = parts[2]
        isNewGroupOptionSelected = folderId === NEW_GROUP_ID
      } else {
        selectedWidgetId = this.selectedWidgetId
      }

      const page = this.selectedPage
      bookmarks = this.selectedOpenTabs.map((tab) => {
        return {
          url: tab.url,
          title: tab.title,
          folderTitle: folderId === NEW_GROUP_ID ? this.newFolderTitle || null : null,
          folderId
        }
      })

      this.isSaving = true

      if (this.isNewWidgetOptionSelected) {
        const widget = {
          widget_type: 'urllist',
          column: 0,
          title: this.newWidgetTitle
        }

        return this.addWidget({ pagePublicId: page.publicId, pageId: page.id, widget })
          .then((data = {}) => {
            if (data && data.id) this.addLinks(page.publicId, data.id, bookmarks)
          })
      } else {
        this.addLinks(page.publicId, selectedWidgetId, bookmarks, folderId, isNewGroupOptionSelected)
      }
    },

    sendChanges () {
      if (this.selectedWidgetId === null) {
        this.isSaving = true

        if (this.autosavedLink) {
          const bookmarkData = this.autosavedLink
          bookmarkData.url = this.url
          bookmarkData.title = this.title
          bookmarkData.description = this.description

          this.editAutosavedLink({
            id: bookmarkData.item_id,
            bookmarkData
          })
            .then(() => {
              this.isSaving = false
              this.setCookieData(null)
              this.quit()
            })
            .catch(() => {})
        } else {
          const bookmarkData = {
            url: this.url,
            title: this.title,
            description: this.description,
          }
          this.autosaveLink(bookmarkData)
            .then(() => {
              this.isSaving = false
              this.setCookieData(null)
              this.quit()
            })
            .catch(() => {})
        }

        return
      }

      let isNewGroupOptionSelected = false
      let selectedWidgetId
      let folderId

      if (typeof this.selectedWidgetId === 'string') {
        const parts = this.selectedWidgetId.match(ID_REGEXP)

        if (!parts) return

        selectedWidgetId = parts[1]
        folderId = parts[2]
        isNewGroupOptionSelected = folderId === NEW_GROUP_ID
      } else {
        selectedWidgetId = this.selectedWidgetId
      }

      const page = this.selectedPage
      const bookmark = {
        url: this.url,
        title: this.title,
        folderTitle: folderId === NEW_GROUP_ID ? this.newFolderTitle || null : null,
        folderId
      }

      if (this.description) bookmark.description = this.description
      if (this.autosavedLink && this.autosavedLink.item_id) bookmark.unsortedId = this.autosavedLink.item_id

      this.isSaving = true

      if (this.isNewWidgetOptionSelected) {
        const widget = {
          widget_type: 'urllist',
          column: 0,
          title: this.newWidgetTitle
        }

        return this.addWidget({ pagePublicId: page.publicId, pageId: page.id, widget })
          .then((data = {}) => {
            if (data && data.id) this.addLink(page.publicId, data.id, bookmark)
          })
      } else {
        this.addLink(page.publicId, selectedWidgetId, bookmark, folderId, isNewGroupOptionSelected)
      }
    },

    setCookieData (widgetId, folderId) {
      if (widgetId === null) chrome.storage.local.set({ [LAST_USED_WIDGET_ID_COOKIE_NAME]: null })
      else {
        const savedId = folderId ? `${widgetId}_${folderId}` : Number(widgetId)
        chrome.storage.local.set({ [LAST_USED_WIDGET_ID_COOKIE_NAME]: savedId })
      }
    },

    addLink (publicId, widgetId, bookmark, folderId, isNewGroup = false) {
      // Store used widget ID in cookie, so same widget is selected by default next time
      if (!isNewGroup) this.setCookieData(widgetId, folderId)

      this.addNewBookmark({ widgetId, bookmark })
        .then((data = {}) => {
          this.isSaving = false

          if (isNewGroup && data.group_id) this.setCookieData(widgetId, data.group_id)
          if (this.isNewWidgetOptionSelected || isNewGroup) this.refreshPagesList()

          this.quit()
        })
    },

    addLinks (publicId, widgetId, bookmarks, folderId, isNewGroup = false) {
      // Store used widget ID in cookie, so same widget is selected by default next time
      if (!isNewGroup) this.setCookieData(widgetId, folderId)

      this.addBookmarksBatch({ widgetId, bookmarks })
        .then((data = {}) => {
          this.isSaving = false

          if (isNewGroup && data.group_id) this.setCookieData(widgetId, data.group_id)
          if (this.isNewWidgetOptionSelected || isNewGroup) this.refreshPagesList()
          if (this.getShouldCloseTabs) chrome.tabs.remove(this.selectedOpenTabsArray)

          this.quit()
        })
    },

    quit () {
      // In mobile browsers that don't close the window, show the bookmarked page
      if (window.location && window.location.replace) window.location.replace(this.url)
      window.close()
    },

    refreshPagesList () {
      this.isRefreshing = true
      this.fetchAllPages({ isRequestForced: true, noCache: true })
        .then(() => {
          this.isRefreshing = false
          this.usedCache = false
        })
        .catch(() => {
          this.isRefreshing = false
        })
    },

    setWidgetId (newID) {
      const oldID = this.selectedWidgetId

      if (newID === oldID) return

      this.saveID(oldID)

      this.selectedWidgetId = newID
    },

    openAdditionalFields () {
      this.showAdditionalFields = true
    },

    closeAdditionalFields () {
      this.showAdditionalFields = false
    },

    expandSelect () {
      this.resetWidgetFieldError()
      this.isSelectExpanded = true
    },

    collapseSelect () {
      this.filterString = ''
      this.isSelectExpanded = false
    },

    openBookmarksImporting () {
      this.isImportingBookmarks = true
    },

    closeBookmarksImporting () {
      this.isImportingBookmarks = false
    },

    openPreferencesEditing () {
      this.isEditingPreferences = true
    },

    closePreferencesEditing () {
      if (!this.isOptionsScreen) this.isEditingPreferences = false
    },

    setLinkExists (existData = null) {
      this.linkExists = existData
    },

    resetLinkExists () {
      this.linkExists = null
    },

    removeAutosaved () {
      if (!this.autosavedLink) return

      this.removeAutosavedLink(this.autosavedLink.item_id)
      this.autosavedLink = null
      this.autosavedLinkRemoved = true
    },

    initImport () {
      chrome.permissions.contains({ permissions: ['bookmarks'] })
        .then((granted) => {
          if (granted) {
            return Promise.resolve(true)
          } else {
            return chrome.permissions.request({ permissions: ['bookmarks'] })
          }
        })
        .then((granted) => {
          if (granted) chrome.runtime.sendMessage({ message: SM_MESSAGE_IMPORT })
        })
        .catch(() => { })
    },

    emitSignOut () {
      if (!this.getUserData) return

      const isAnonymous = this.getUserData.is_anonymous || this.getUserData.dummy_email

      if (!isAnonymous || window.confirm(this.$t('logout_confirm').message)) {
        this.resetAll()
        chrome.runtime.sendMessage({ message: SM_MESSAGE_SIGN_OUT })
      }
    },

    resetAll (options = {}) {
      this.newWidgetTitle = ''
      this.newFolderTitle = ''
      this.savedWidgetId = null
      this.selectedWidgetId = null
      this.homepageId = null
      this.userPages = []
      this.homepageOptions = []
      this.$store.dispatch('clearStorage')
      chrome.storage.local.set({ [LAST_USED_WIDGET_ID_COOKIE_NAME]: null })

      if (options.fetchPages) this.refreshPagesList()
    },

    openChangeShortcuts () {
      if (WINDOW_CURRENT_BROWSER === 'firefox') chrome.tabs.create({ url: 'https://support.start.me/hc/en-us/articles/360018117140' })
      else chrome.tabs.create({ url: 'chrome://extensions/shortcuts' })

      this.quit()
    },

    showHelpPage () {
      chrome.runtime.sendMessage({ message: SM_MESSAGE_SHOW_HELP_PAGE })
      this.quit()
    },

    clearCache () {
      this.isClearingCache = true

      chrome.runtime.sendMessage({ message: SM_MESSAGE_CLEAR_CACHE })
      this.resetAll({ fetchPages: true })

      setTimeout(() => { this.isClearingCache = false }, 2000)

      this.initPages()
    },

    checkTabsPermission () {
      chrome.permissions.contains({ permissions: ['tabs'] })
        .then((granted) => {
          if (granted) {
            return Promise.resolve(true)
          } else {
            return Promise.reject('No Tabs permission granted')
          }
        })
        .then(async (granted) => {
          if (granted) {
            const current = await chrome.windows.getCurrent()
            const allTabs = await chrome.tabs.query({})

            this.openTabsArray = allTabs.filter(tab => tab.windowId === current.id)
            this.isTabsPermissionGranted = true
          }
        })
        .catch(() => { })
    },

    requestTabsPermission () {
      chrome.permissions.request({ permissions: ['tabs'] })
        .then(async (granted) => {
          if (granted) {
            const current = await chrome.windows.getCurrent()
            const allTabs = await chrome.tabs.query({})

            this.openTabsArray = allTabs.filter(tab => tab.windowId === current.id)
            this.isTabsPermissionGranted = true
          }
        })
        .catch(() => { })
    },

    openOpenTabsScreen () {
      if (!this.isTabsPermissionGranted) {
        this.requestTabsPermission()
        return
      }

      this.isOpenTabsOpened = true
    },

    closeOpenTabsScreen () {
      this.isOpenTabsOpened = false
    },

    toggleOpenTabSelected (tab = {}) {
      if (this.selectedOpenTabsArray.includes(tab.id)) this.selectedOpenTabsArray = this.selectedOpenTabsArray.filter(id => id !== tab.id)
      else this.selectedOpenTabsArray.push(tab.id)
    },

    toggleSelectAllTabs () {
      if (this.allOpenTabsSelected) this.selectedOpenTabsArray = []
      else this.selectedOpenTabsArray = this.openTabsArray.map(tab => tab.id)
    },

    getOpenTabIcon (openTab = {}) {
      if (openTab.favIconUrl) return openTab.favIconUrl

      try {
        const url = new URL(openTab.url)
        return `https://f.start.me/${url.hostname}`
      } catch (e) {
        return 'https://f.start.me/fa/bookmark'
      }
    },

    getBookmarkIcon () {
      try {
        const url = new URL(this.url)
        return `https://f.start.me/${url.hostname}`
      } catch (e) {
        return 'https://f.start.me/fa/bookmark'
      }
    },

    showUserMenu () {
      this.isUserMenuVisible = true
    },

    hideUserMenu () {
      this.isUserMenuVisible = false
    },
  }
}
</script>

