                type="submit"
                :disabled="isSaving || !isButtonActive"
              >
                <ThreeDotsSpinner v-if="isSaving" />
                <template v-else>{{ $t('save_bookmark').message }}</template>
              </button>

              <button
                class="add-bookmark-page__secondary-button"
                v-if="!hideOpenTabs"
                :class="{ 'add-bookmark-page__secondary-button_disabled': (isTabsPermissionGranted && !isOpenTabsButtonActive) || isSaving }"
                :disabled="(isTabsPermissionGranted && !isOpenTabsButtonActive) || isSaving"
                @click.prevent.stop="openOpenTabsScreen"
              >
                <template v-if="isTabsPermissionGranted">
                  {{ $t('open_tabs').message }}&nbsp;({{ openTabsArray.length }})
                  <SvgStrokeIcon
                    class="add-bookmark-page__feather-icon add-bookmark-page__feather-icon_lowered"
                    icon="chevron-right"
                    :options="{ size: 14, strokeWidth: 1 }"
                  />
                </template>
                <template v-else>{{ $t('open_tabs').message }}</template>
              </button>
            </div>
          </form>
        </div> <!-- /MAIN SCREEN -->

        <!-- OPEN TABS SCREEN -->
        <transition name="slide-left" in-out>
          <div
            class="add-bookmark-page__aside add-bookmark-page__aside_bottom"
            v-if="isOpenTabsOpened"
            key="open_tabs_screen"
          >
            <div class="add-bookmark-page__container">
              <HeaderBar
                class="add-bookmark-page__header"
                is-aside
                has-preferences-button
                :is-refreshing="isRefreshing"
                :profile-avatar="profileAvatar"
                :trail-string="`${ $t('open_tabs').message } (${ openTabsArray.length })`"
                @logoClicked="closeOpenTabsScreen"
                @openPreferences="openPreferencesEditing"
                @userButtonClicked="showUserMenu"
              />

              <form
                class="add-bookmark-page__form add-bookmark-page__form_strict"
                @submit.prevent="sendChangesMultiple"
              >
                <div class="add-bookmark-page__row">
                  <div class="add-bookmark-page__line">
                    <div
                      class="add-bookmark-page__selector add-bookmark-page__selector_all"
                      @click.stop.prevent="toggleSelectAllTabs"
                    >
                      <button
                        class="add-bookmark-page__control"
                        @click.stop.prevent="toggleSelectAllTabs"
                      >
                        <span
                          class="add-bookmark-page__check"
                          :class="{ 'add-bookmark-page__check_active': allOpenTabsSelected }"
                        >
                          <SvgStrokeIcon
                            class="add-bookmark-page__check-icon"
                            icon="check"
                            :options="{ size: 12, strokeWidth: 2 }"
                          />
                        </span>
                      </button>

                      <span class="add-bookmark-page__control-label">{{ $t('select_all').message }}</span>
                    </div>

                    <div
                      class="add-bookmark-page__selector"
                      v-for="openTab in openTabsArray"
                      :key="`open_tab_${openTab.id}`"
                    >
                      <a
                        class="add-bookmark-page__control"
                        @click.stop.prevent="toggleOpenTabSelected(openTab)"
                      >
                        <span
                          class="add-bookmark-page__check"
                          :class="{ 'add-bookmark-page__check_active': selectedOpenTabsArray.includes(openTab.id) }"
                        >
                          <SvgStrokeIcon
                            class="add-bookmark-page__check-icon"
                            icon="check"
                            :options="{ size: 12, strokeWidth: 2 }"
                          />
                        </span>
                      </a>

                      <span class="add-bookmark-page__control-favicon-wrapper">
                        <img
                          class="add-bookmark-page__control-favicon"
                          alt=""
                          :src="getOpenTabIcon(openTab)"
                          onerror="this.src='https://f.start.me/fa/bookmark';this.onerror='';"
                        />
                      </span>

                      <div class="add-bookmark-page__title-container add-bookmark-page__title-container_flexible">
                        <input
                          class="add-bookmark-page__title-input"
                          type="text"
                          :ref="`openTabsTitleInput_${openTab.id}`"
                          v-model="openTab.title"
                          @focus="editOpenTabsTitle(openTab.id)"
                          @keydown.enter.stop.prevent="toggleOpenTabSelected(openTab)"
                        />
                        <span class="add-bookmark-page__title-button">
                          <SvgStrokeIcon
                            class="add-bookmark-page__feather-icon add-bookmark-page__feather-icon_lowered"
                            icon="edit-3"
                          />
                        </span>
                      </div>
                    </div>
                  </div>
                </div>

                <div class="add-bookmark-page__destination">
                  <template v-if="isNewWidgetOptionSelected">
                    <div class="add-bookmark-page__label">{{ $t('create_new_widget_on_page_ext').message }} {{ `"${selectedPage.label || ''}"` }}</div>
                    <div class="add-bookmark-page__input-wrapper">
                      <input
                        class="add-bookmark-page__input add-bookmark-page__input_padded"
                        type="text"
                        required
                        autofocus
                        v-model="newWidgetTitle"
                      >
                      <a
                        class="add-bookmark-page__cancel"
                        @click="restoreID"
                      >
                        <SvgStrokeIcon
                          class="add-bookmark-page__control-icon add-bookmark-page__control-icon_lowered"
                          icon="x"
                          :options="{ size: 16 }"
                        />
                      </a>
                    </div>
                  </template>
                  <template v-else-if="isNewGroupOptionSelected">
                    <div class="add-bookmark-page__label">{{ $t('create_new_group_on_page_widget_ext').message }} {{ `"${selectedPage.label || ''}" - "${selectedPage.selectedWidget ? selectedPage.selectedWidget.label || '' : ''}"` }}</div>
                    <div class="add-bookmark-page__input-wrapper">
                      <input
                        class="add-bookmark-page__input add-bookmark-page__input_padded"
                        type="text"
                        required
                        autofocus
                        v-model="newFolderTitle"
                      >
                      <a
                        class="add-bookmark-page__cancel"
                        @click="restoreID"
                      >
                        <SvgStrokeIcon
                          class="add-bookmark-page__control-icon add-bookmark-page__control-icon_lowered"
                          icon="x"
                          :options="{ size: 16 }"
                        />
                      </a>
                    </div>
                  </template>
                  <template v-else>
                    <SearchableSelect
                      class="add-bookmark-page__dropdown add-bookmark-page__dropdown_hoverable"
                      v-model="filterString"
                      :selected-value="selectedWidgetId"
                      :options="pages"
                      :max-height="dropdownHeight"
                      :has-error="hasWidgetFieldError"
                      @activate="expandSelect"
                      @set-value="setWidgetId"
                    />
                  </template>
                </div>

                <div class="add-bookmark-page__buttons add-bookmark-page__buttons_position_left">
                  <button
                    class="add-bookmark-page__main-button add-bookmark-page__main-button_wrapped"
                    :class="{ 'add-bookmark-page__main-button_disabled': !selectedOpenTabsArray.length }"
                    type="submit"
                    :disabled="isSaving || !selectedOpenTabsArray.length"
                  >
                    <ThreeDotsSpinner v-if="isSaving" />
                    <template v-else>{{ `${$t('save_selected_tabs').message}${selectedOpenTabsArray.length ? ' (' + selectedOpenTabsArray.length + ')' : ''}` }}</template>
                  </button>

                  <div
                    class="add-bookmark-page__extra"
                    @click.stop.prevent="toggleCloseTabs"
                  >
                    <button
                      class="add-bookmark-page__control"
                      @click.stop.prevent="toggleCloseTabs"
                    >
                      <span
                        class="add-bookmark-page__check"
                        :class="{ 'add-bookmark-page__check_active': getShouldCloseTabs }"
                      >
                        <SvgStrokeIcon
                          class="add-bookmark-page__check-icon"
                          icon="check"
                          :options="{ size: 12, strokeWidth: 2 }"
                        />
                      </span>
                    </button>
                    <span class="add-bookmark-page__control-label">{{ $t('close_tabs_after_saving').message }}</span>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </transition> <!-- /OPEN TABS SCREEN -->
      </div>
    </div>
  </div>
</template>

