<script>
import vueTypes from 'vue-types'
import { mapGetters } from 'vuex'
import shortId from 'shortid'
import PremiumMark from '@/components/PremiumMark'
import SvgStrokeIcon from '@/components/SvgStrokeIcon'

export default {
  name: 'Toggler',

  components: {
    PremiumMark,
    SvgStrokeIcon
  },

  props: {
    value: vueTypes.any.isRequired,
    options: vueTypes.shape({
      label: vueTypes.string,
      defaultColor: vueTypes.string,
      isOpposite: vueTypes.bool,
      isDivided: vueTypes.bool
    }).loose.def({
      label: '',
      defaultColor: '',
      isOpposite: false,
      isDivided: false
    })
  },

  data () {
    return { currentValue: this.value }
  },

  computed: {
    ...mapGetters(['isDarkTheme']),
    isAdmin () {
      return this.$route.meta.isAdminPage
    },
    checkedValue: {
      get () {
        return this.currentValue
      },
      set (newValue) {
        if (this.options.isDisabled) {
          if (this.options.disabledHandler) this.options.disabledHandler()

          return
        }

        this.currentValue = +newValue
        this.$emit('input', +this.currentValue)
      }
    },

    id () {
      return shortId.generate()
    }
  },

  methods: {
    emitChange () {
      this.$emit('change')
    }
  }
}
</script>

<template>
  <div
    class="toggler"
    :class="{
      'toggler_checked': options.isOpposite ? (options.ignoreConfirm ? !currentValue : !value) : (options.ignoreConfirm ? currentValue : value),
      'toggler_divided': options.isDivided,
      'toggler_disabled': options.isDisabled,
      'toggler_theme_dark': isDarkTheme && !isAdmin,
      'toggler_increased-background': options.hasIncreasedBackground,
    }"
  >
    <input
      class="toggler__input"
      type="checkbox"
      :id="id"
      v-model="checkedValue"
      @change="emitChange"
    >
    <label
      class="toggler__label"
      :for="id"
    >
      <span class="toggler__label-container">
        <span
          class="toggler__icon-container"
          v-if="options.icon"
        >
          <SvgStrokeIcon
            class="toggler__icon"
            :icon="options.icon"
          />
        </span>
        <span class="toggler__text">
          <span class="toggler__title">{{ options.label }}</span>
          <span
            class="toggler__subtitle"
            v-if="options.sublabel"
          >{{ options.sublabel }}</span>
        </span>

        <span
          class="toggler__premium-mark"
          v-if="options.isForPro"
        >
          <PremiumMark
            :is-active="options.isProUser"
            :label="$t('account_types.pro').message"
          />
        </span>
      </span>

      <span
        class="toggler__control"
        :class="`toggler__control_color_${options.defaultColor}`"
      >
        <span class="toggler__thumb" />
      </span>
    </label>
  </div>
</template>

<style lang="scss">
.toggler {
  width: 100%;
  height: 100%;
  color: inherit;

  &_divided {
    padding-bottom: 17px;
    border-bottom: 1px solid $color-lightest-grey;
  }

  &_divided.toggler_theme_dark {
    border-bottom: 1px solid $color-darkest-grey;
  }

  &__input {
    display: none;
  }

  &__label {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    color: inherit !important;
    cursor: pointer;
  }

  &_increased-background &__label {
    box-sizing: border-box;
    padding: 5px 0;
    margin: -5px 0;
    overflow: hidden;
    color: $color-middle-black;
    border-radius: 4px;
    transition: 200ms background-color ease-out;
  }

  &__label-container {
    display: flex;
    flex: 1 1 100%;
    align-items: center;
    min-height: 32px;
  }

  &__icon-container {
    display: flex;
    flex: 0 0 32px;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    overflow: hidden;
    background-color: $color-fog-grey;
    border-radius: 50%;
  }

  &__icon {
    stroke: $color-middle-black;
  }

  &__text {
    display: block;
    font-family: $segoe;
    font-weight: normal;
  }

  &__icon-container + &__text {
    margin-left: 14px;
  }

  &__title {
    display: block;
    font-size: 14px;
    line-height: 19px;
  }

  &__subtitle {
    display: block;
    font-size: 12px;
    line-height: 15px;
    opacity: 0.5;
  }

  &__control {
    position: relative;
    display: flex;
    align-items: center;
    flex: 0 0 32px;
    width: 32px;
    height: 18px;
    background-color: $color-grey;
    border-radius: 12px;
    transition: background-color 300ms ease;
  }

  &_checked &__control {
    background-color: $color-blue;
  }

  &_disabled &__control {
    opacity: 0.2;
  }

  &__thumb {
    display: block;
    width: 16px;
    height: 16px;
    margin: 0 1px;
    background-color: $color-white;
    border-radius: 50%;
    transition: transform 300ms ease;
  }

  &_checked &__thumb {
    transform: translateX(14px);
  }

  &__label-container + &__control {
    margin-left: 12px;
  }

  &__premium-mark {
    display: inline-flex;
    margin-left: 6px;
  }
}
</style>
