<script>
import vueTypes from 'vue-types'

export default {
  name: 'MarkdownTextarea',

  props: {
    options: vueTypes.shape({
      hasAutofocus: vueTypes.bool.def(false),
      hasLink: vueTypes.bool.def(false),
      isLarge: vueTypes.bool.def(false)
    }).loose.def({
      hasAutofocus: false,
      isLarge: false
    }),
    value: vueTypes.string.def('')
  },

  data () {
    return {
      currentValue: this.value
    }
  },

  computed: {
    style () {
      if (!this.options.maxHeight) return {}

      return {
        minHeight: `${this.options.maxHeight}px`,
        maxHeight: `${this.options.maxHeight}px`,
        overflow: 'hidden auto'
      }
    }
  },

  watch: {
    value () {
      this.currentValue = this.value
    }
  },

  mounted () {
    if (this.options.hasAutofocus) {
      this.$refs.textarea.$el.focus()
      this.$refs.textarea.$el.selectionStart = 10000
    }
  },

  methods: {
    emitInput () {
      this.$emit('input', this.currentValue)
    }
  }
}
</script>

<template>
  <div class="markdown-textarea">
    <textarea-autosize
      class="markdown-textarea__field"
      :class="{ 'markdown-textarea__field_font_small': options.isSmall }"
      :rows="options.isLarge ? 6 : 2"
      :style="style"
      v-model="currentValue"
      ref="textarea"
      @input="emitInput"
      @paste.native.stop
    />
    <span
      class="markdown-textarea__tip"
      v-if="options.tip"
    >{{ options.tip }}</span>
  </div>
</template>

<style lang="scss">
  .markdown-textarea {
    position: relative;

    &__field {
      box-sizing: border-box;
      display: block;
      width: 100%;
      min-height: 2em;
      padding: 9px 12px;
      font-family: $segoe;
      font-size: 14px;
      line-height: 19px;
      background-color: $color-white;
      border: 1px solid $color-cloud-grey;
      border-radius: 4px;
      outline: none;
    }

    &__field:focus {
      border-color: $color-light-browny-grey;
    }

    &__field_font_small {
      font-size: 12px;
    }

    &__tip {
      display: block;
      height: 15px;
      padding-top: 2px;
      font-size: 11px;
      line-height: 15px;
      color: $color-secondary-grey;
      text-align: right;
    }
  }
</style>
