<script>
import vueTypes from 'vue-types'
import SvgStrokeIcon from '@/components/SvgStrokeIcon'

export default {
  name: 'HeaderBar',

  components: { SvgStrokeIcon },

  props: {
    trailString: vueTypes.string.def(''),
    profileAvatar: vueTypes.string.def(''),
    isAside: vueTypes.bool.def(false),
    hasPreferencesButton: vueTypes.bool.def(false),
    hasHelpButton: vueTypes.bool.def(false),
    hasRefreshButton: vueTypes.bool.def(false),
    hasTabs: vueTypes.bool.def(false),
    hasMainLogo: vueTypes.bool.def(false),
    isRefreshing: vueTypes.bool.def(false),
    isOptionsScreen: vueTypes.bool.def(false),
  },

  methods: {
    emitLogoClicked () {
      this.$emit('logoClicked')
    },
    emitRefresh () {
      this.$emit('refresh')
    },
    emitOpenPreferences () {
      this.$emit('openPreferences')
    },
    emitUserButtonClicked () {
      this.$emit('userButtonClicked')
    },
    emitHelpClicked () {
      this.$emit('helpClicked')
    },
  },
}
</script>

<template>
  <header
    class="header-bar"
    :class="{
      'header-bar_aside': isAside,
      'header-bar_has-tabs': hasTabs,
      'header-bar_cut': isOptionsScreen,
    }"
  >
    <div
      class="header-bar__header-wrapper"
      v-if="!isOptionsScreen"
    >
      <div class="header-bar__header-logo">
        <a
          class="header-bar__image-container"
          href="https://start.me"
          target="_blank"
          v-if="hasMainLogo"
        >
          <img
            class="header-bar__logo-image"
            src="/images/Logo@2x.png"
            alt=""
          >
        </a>
        <div
          class="header-bar__image-container header-bar__image-container_interactive"
          v-else
          @click.stop="emitLogoClicked"
        >
          <img
            class="header-bar__logo-image"
            src="/images/Logo-app@2x.png"
            alt=""
          >
        </div>

        <template v-if="trailString">
          <SvgStrokeIcon
            class="header-bar__control-icon"
            icon="chevron-right"
            :options="{ size: 14, strokeWidth: 1 }"
          />
          <div class="header-bar__trail">{{ trailString }}</div>
        </template>
      </div>

      <div class="header-bar__extra">
        <slot />
      </div>

      <span
        class="header-bar__control-button header-bar__control-button_small"
        v-if="hasRefreshButton"
        :class="{ 'header-bar__control-button_animated': isRefreshing }"
        :title="$t('refresh_list').message"
        @click.stop="emitRefresh"
      >
        <SvgStrokeIcon
          class="header-bar__control-icon"
          icon="refresh-cw"
          :options="{ strokeWidth: 1 }"
        />
      </span>

      <span
        class="header-bar__control-button header-bar__control-button_small"
        v-if="hasHelpButton"
        @click.stop="emitHelpClicked"
      >
        <SvgStrokeIcon
          class="header-bar__control-icon"
          icon="help-circle"
          :options="{ strokeWidth: 1 }"
        />
      </span>

      <span
        class="header-bar__control-button header-bar__control-button_small"
        v-if="hasPreferencesButton"
        @click.stop="emitOpenPreferences"
      >
        <SvgStrokeIcon
          class="header-bar__control-icon"
          icon="settings"
          :options="{ strokeWidth: 1 }"
        />
      </span>

      <span
        class="header-bar__control-button header-bar__control-button_round"
        :class="{ 'header-bar__control-button_grey header-bar__control-button_medium': !profileAvatar }"
        @click.stop="emitUserButtonClicked"
      >
        <img
          class="header-bar__avatar"
          alt=""
          v-if="profileAvatar"
          :src="profileAvatar"
        >
        <SvgStrokeIcon
          class="header-bar__control-icon header-bar__control-icon_white"
          icon="user"
          :options="{ strokeWidth: 1 }"
        />
      </span>
    </div>

    <slot
      name="tabs"
      v-if="hasTabs"
    />
  </header>
</template>

