<script>
import Bookmarklet from '@/components/AddBookmarkPage.vue'

const SM_MESSAGE_CONTEXT_LINK = 'contextLinkRequest'

export default {
  name: 'PopupApp',

  components: { Bookmarklet },

  data () {
    return {
      propUrl: '',
      propTitle: '',
      hideOpenTabs: false,
    }
  },

  computed: {
    isOptionsScreen () {
      const re = /^chrome-extension:\/\/(\S+)\/popup.html#options$/

      return re.test(window.location.href)
    },
  },

  beforeMount () {
    this.requireUser()
  },

  methods: {
    requireUser () {
      this.$startme.requireUser()
        .then(user => {
          if (!this.$store.state.user || (this.$store.state.user.id && user && user.id && this.$store.state.user.id !== user.id)) {
            this.$store.dispatch('clearAllPages')
            this.$startme.clearPages()
          }

          this.$store.dispatch('setUser', user)

          if (this.isOptionsScreen) return

          this.requestContextLink()
            .catch(() => {
              chrome.tabs.query({ active: true, currentWindow: true })
                .then((tabs) => {
                const tab = tabs[0]
                if (tab) {
                  let { url, title } = tab

                  if (!url) return

                  this.propUrl = (url || '').replace(/\/+$/, "")
                  this.propTitle = title || this.getHostname(url)
                }
              })
              .catch(() => { })
            })
        })
        .catch(() => { })
    },

    requestContextLink () {
      return new Promise((resolve, reject) => {
        chrome.runtime.sendMessage({ message: SM_MESSAGE_CONTEXT_LINK })
          .then((link) => {
            if (link && link.url) {
              this.propUrl = link.url.replace(/\/+$/, "")
              this.propTitle = link.title || this.getHostname(link.url)
              this.hideOpenTabs = true
              resolve()
            } else reject()
          })
          .catch(reject)
      })
    },

    getHostname (url) {
      try {
        const urlObj = new URL(url)
        return urlObj.hostname || url
      } catch (e) {
        return url
      }
    },
  },
}
</script>

<template>
  <Bookmarklet v-bind="{ propUrl, propTitle, isOptionsScreen, hideOpenTabs }" />
</template>

<style>
html,
body {
  width: 500px;
  height: 500px;
  margin: 0;
}
</style>
