<script>
import vueTypes from 'vue-types'
import SvgStrokeIcon from '@/components/SvgStrokeIcon'

const DEFAULT_PLACEHOLDER = 'select_a_widget'
const SEARCH_PLACEHOLDER = 'search_widgets'
const FOCUS_DELAY = 500

export default {
  name: 'SearchableSelect',

  components: {
    SvgStrokeIcon
  },

  props: {
    options: vueTypes.array.def([]),
    selectedValue: vueTypes.any.def(null),
    maxHeight: vueTypes.number,
    value: vueTypes.string.def(''),
    isFunctional: vueTypes.bool.def(false),
    hasError: vueTypes.bool.def(false)
  },

  data () {
    return {
      isActive: this.isFunctional || false,
      expandedPages: [],
      expandedWidgets: []
    }
  },

  computed: {
    optionsList () {
      const result = []

      this.options.forEach((group) => {
        group.value.forEach((subgroup) => {
          const isExpanded = this.expandedPages.includes(subgroup.id) || this.filterString.length

          result.push({
            label: this.getSubgroupLabel(group.label, subgroup.label),
            key: `page_${subgroup.id}`,
            level: 'page',
            clickHandler: () => { this.togglePage(subgroup.id) },
            isExpandable: true,
            isExpanded,
          })

          if (isExpanded) {
            subgroup.value.forEach((inner) => {
              const isExpandable = Array.isArray(inner.value)
              const isExpanded = this.expandedWidgets.includes(inner.id) || this.filterString.length
              const isSelected = inner.id === this.selectedValue

              result.push({
                label: inner.label,
                key: `widget_${inner.id}`,
                level: 'inner',
                isDimmed: inner.isDimmed,
                clickHandler: () => {
                  if (isExpandable) {
                    this.toggleWidget(inner.id)
                  } else {
                    this.setValue(inner.id)
                  }
                },
                isExpandable,
                isExpanded,
                isSelected,
              })

              if (isExpandable && isExpanded) {
                inner.value.forEach((folder) => {
                  const isSelected = folder.id === this.selectedValue

                  result.push({
                    label: folder.label,
                    key: `folder_${folder.id}`,
                    level: 'folder',
                    isDimmed: folder.isDimmed,
                    clickHandler: () => { this.setValue(folder.id) },
                    isSelected,
                  })
                })
              }
            })
          }
        })
      })

      return result
    },
    placeholder () {
      if (this.isFunctional) return this.$t(SEARCH_PLACEHOLDER).message

      const placeholder = this.getPlaceholder(this.selectedValue)

      return placeholder
    },
    filterString: {
      get () {
        return this.value
      },
      set (str) {
        this.$emit('input', str)
      }
    }
  },

  created () {
    if (this.isFunctional) this.$eventBus.$on('activate-searchable-input', this.processActivation)
  },

  mounted () {
    this.processActivation()

    if (this.$isMobile || !this.isFunctional) return

    setTimeout(() => {
      this.setFocus()
    }, FOCUS_DELAY)
  },

  beforeDestroy () {
    if (this.isFunctional) this.$eventBus.$off('activate-searchable-input', this.processActivation)
  },

  methods: {
    resetFilter () {
      this.filterString = ''
    },
    getPlaceholder (id) {
      if (id === null) return this.$t('inbox').message

      let widgetId
      let groupId

      if (typeof id === 'string') {
        const re = /^(\d+)_(\d+)/
        const parts = id.match(re)

        if (!parts) return this.$t(DEFAULT_PLACEHOLDER).message

        widgetId = Number(parts[1])
        groupId = Number(parts[2])
      } else {
        widgetId = id
      }

      let placeholder = ''
      let pageLabel
      let widgetLabel
      let groupLabel

      this.options.some((o) => {
        return o.value.some((p) => {
          return p.value.some((w) => {
            if (w.id === widgetId) {
              pageLabel = p.label
              widgetLabel = w.label

              if (groupId) {
                w.value.some((g) => {
                  if (g.id === id) {
                    groupLabel = g.label
                    return true
                  }

                  return false
                })
              }

              return true
            }

            return false
          })
        })
      })

      if (pageLabel !== undefined && widgetLabel !== undefined) {
        placeholder = `${pageLabel} / ${widgetLabel}`

        if (groupLabel) placeholder += ` / ${groupLabel}`
      }

      return placeholder || this.$t(DEFAULT_PLACEHOLDER).message
    },
    setFocus () {
      if (!this.$refs.input) return

      this.$refs.input.focus()
    },

    processActivation () {
      this.preExpandItems()
      this.$nextTick(() => {
        this.scrollList()
      })
    },
    preExpandItems () {
      const pageLimit = 2
      let pagesCount = 0

      const lessThreePages = this.options.every((group) => {
        pagesCount += group.value.length

        if (pagesCount > pageLimit) return false
        return true
      })

      if (lessThreePages) {
        this.options.forEach((group) => {
          group.value.forEach((page) => {
            this.expandedPages.push(page.id)
          })
        })
      }

      if (this.selectedValue) {
        this.options.some((group) => {
          return group.value.some((page) => {
            if (page.value.length === 1) return false

            return page.value.some((widget) => {
              if (widget.id === this.selectedValue) {
                if (!this.expandedPages.includes(page.id)) this.expandedPages.push(page.id)
                return true
              }

              if (!Array.isArray(widget.value) || widget.value.length === 1) return false

              return widget.value.some((folder) => {
                if (folder.id === this.selectedValue) {
                  if (!this.expandedPages.includes(page.id)) this.expandedPages.push(page.id)
                  if (!this.expandedWidgets.includes(widget.id)) this.expandedWidgets.push(widget.id)
                  return true
                }

                return false
              })
            })
          })
        })
      }
    },
    scrollList () {
      if (!this.$refs.list || !this.$refs.selected) return

      const list = Array.isArray(this.$refs.list) ? this.$refs.list[0] : this.$refs.list
      const selected = Array.isArray(this.$refs.selected) ? this.$refs.selected[0] : this.$refs.selected

      if (!selected || !list) return

      const listCoords = list.getBoundingClientRect()
      const selectedCoords = selected.getBoundingClientRect()

      const scrollValue = selectedCoords.top - listCoords.top + list.scrollTop - this.maxHeight / 2

      list.scrollTop = scrollValue >= 0 ? scrollValue : 0
    },
    deactivate () {
      this.$emit('input', '')
      this.$emit('deactivate')
    },
    setValue (id) {
      this.$emit('set-value', id)
      this.deactivate()
    },
    setValueAndSave (id) {
      this.$emit('set-value-and-save', id)
      this.deactivate()
    },
    emitActivation () {
      if (this.isFunctional) return

      this.$emit('activate')
    },
    getSubgroupLabel (groupLabel, subgroupLabel) {
      if (!groupLabel) return subgroupLabel

      return `${groupLabel}: ${subgroupLabel}`
    },
    togglePage (id) {
      if (this.expandedPages.includes(id)) this.expandedPages = this.expandedPages.filter(item => item !== id)
      else this.expandedPages.push(id)
    },
    toggleWidget (id) {
      if (this.expandedWidgets.includes(id)) this.expandedWidgets = this.expandedWidgets.filter(item => item !== id)
      else this.expandedWidgets.push(id)
    }
  }
}
</script>

<template>
  <div
    :class="[
      'searchable-select',
      {
        'searchable-select_active': isFunctional,
        'searchable-select_filled': filterString.length,
        'searchable-select_error': hasError,
      }
    ]"
  >
    <div
      class="searchable-select__wrapper"
      @click="emitActivation"
    >
      <div class="searchable-select__field">
        <input
          class="searchable-select__input"
          :class="{
            'searchable-select__input_inactive': !isFunctional,
            'searchable-select__input_padded': isFunctional
          }"
          type="text"
          autocomplete="off"
          ref="input"
          :placeholder="placeholder"
          v-model="filterString"
          @keydown.enter.stop.prevent="emitActivation"
        >
        <span
          class="searchable-select__lens"
          v-if="isFunctional"
        >
          <SvgStrokeIcon
            class="searchable-select__feather-icon"
            icon="search"
            :options="{ strokeWidth: 1 }"
          />
        </span>
        <span class="searchable-select__chevron">
          <SvgStrokeIcon
            class="searchable-select__feather-icon"
            icon="chevron-right"
            :options="{ size: 14, strokeWidth: 1 }"
          />
        </span>
        <span
          class="searchable-select__times"
          @click="resetFilter"
        >
          <SvgStrokeIcon
            class="searchable-select__feather-icon"
            icon="x"
            :options="{ size: 14, strokeWidth: 1 }"
          />
        </span>
      </div>
      <div
        class="searchable-select__content"
        v-if="isFunctional"
      >
        <div
          class="searchable-select__list"
          ref="list"
          :style="{ maxHeight: maxHeight ? `${maxHeight}px` : 'none' }"
        >
          <button
            class="searchable-select__option"
            :class="{ 'searchable-select__option_selected': selectedValue === null }"
            :tabindex="0"
            @click.stop="setValue(null)"
          >
            <div class="searchable-select__option-container">
              <span class="searchable-select__item-icon">
                <SvgStrokeIcon
                  class="searchable-select__feather-icon searchable-select__feather-icon_relative"
                  icon="inbox"
                  :options="{ strokeWidth: 1 }"
                />
              </span>
              <span class="searchable-select__item-label">{{ $t('inbox').message }}</span>
            </div>
          </button>
          <button
            class="searchable-select__option"
            v-for="option in optionsList"
            :ref="option.isSelected ? 'selected' : option.key"
            :key="option.key"
            :class="[
              `searchable-select__option_type_${option.level}`,
              {
                'searchable-select__option_expandable': option.isExpandable,
                'searchable-select__option_selected': option.isSelected,
                'searchable-select__option_dimmed': option.isDimmed,
              }
            ]"
            :tabindex="0"
            @click="option.clickHandler"
          >
            <span class="searchable-select__option-container">
              <span class="searchable-select__item-icon">
                <SvgStrokeIcon
                  class="searchable-select__feather-icon searchable-select__feather-icon_relative"
                  v-if="option.isExpandable"
                  icon="chevron-down"
                  :class="{ 'searchable-select__feather-icon_rotated': option.isExpanded }"
                  :options="{ strokeWidth: 1 }"
                />
              </span>
              <span class="searchable-select__item-label">{{ option.label }}</span>
            </span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
