  }

  &__input_inactive::-webkit-input-placeholder {
    color: $color-jet-black;
  }

  &__input_inactive::-moz-placeholder {
    color: $color-jet-black;
  }

  &__input_inactive:-ms-input-placeholder {
    color: $color-jet-black;
  }

  &__lens {
    position: absolute;
    top: 13px;
    left: 10px;
    z-index: 2;
    color: $color-middle-grey;
    pointer-events: none;
  }

  &__input ~ &__times {
    display: none;
  }

  &_filled &__input ~ &__chevron {
    display: none;
  }

  &_filled &__input ~ &__times {
    display: flex;
  }

  &__input,
  &__chevron,
  &__times {
    line-height: 36px;
    cursor: pointer;
  }

  &__chevron,
  &__times {
    display: flex;
    align-items: center;
    padding: 0 12px;
    text-align: center;
  }

  &__times {
    color: rgba($color-middle-black, 0.5);
  }

  &__times:hover {
    color: $color-middle-black;
  }

  &__chevron {
    color: $color-middle-black;
    transition: transform 0.2s ease-out;
  }

  &_active &__chevron {
