    height: calc(100% - 50px);
    padding-top: 10px;
    overflow-y: hidden;
  }

  &_active &__content {
    overflow-y: visible;
  }

  &__list {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 0 0 10px;
    margin: 0;
    overflow-y: auto;
    font-size: 14px;
    color: $color-jet-black;
    background-color: $color-white;
  }

  &__option {
    box-sizing: content-box;
    display: block;
    height: 20px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 20px;
    white-space: nowrap;
    color: $color-dark-blue-grey;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
  }

  &__option_type_inner {
    padding-left: 19px;
  }

  &__option_type_folder {
    padding-left: 35px;
  }

  &__option-container {
