    line-height: 19px;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__item &__item-label {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  &__item-icon + &__item-label {
    margin-left: 4px;
  }

  &__item-icon {
    display: block;
    flex: 0 0 16px;
    width: 16px;
    min-width: 16px;
    line-height: 20px;
    color: $color-middle-black;
    transition: transform 300ms ease-out;
    text-align: left;
  }

  &__item &__item-icon {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  &__item-label + &__item-icon {
    margin-left: 8px;
  }

  &__arrow {
    width: 100%;
  }

  &__inners,
  &__folder-box {
    display: block;
    height: 0;
    overflow: hidden;
  }

  &__item_expanded + &__inners,
  &__inner_expanded + &__folder-box {
    height: auto;
  }

  &__subgroup {
    padding-left: 25px;
    color: $color-dark-blue-grey;
  }

  &__option_selected &__item-label {
    color: $color-jet-black;
    cursor: pointer;
    text-decoration: underline;
  }

  &__inner_header {
    cursor: pointer;
  }

  &__inner_selectable,
  &__inner_header,
  &__folder {
    display: flex;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 19px;
    cursor: pointer;
  }

  &__folder {
    padding-left: 35px;
  }

  &__inner_selectable:hover &__item-label,
  &__inner_header:hover &__item-label,
  &__folder:hover &__item-label {
    text-decoration: underline;
  }

  &__option_dimmed {
    color: $color-middle-grey;
  }

  &__option:focus &__item-label,
  &__option:hover &__item-label {
    text-decoration: underline;
  }
}
</style>
